<template>
    <v-app>
        <iframe id="mapPage" width="100%" height="100%" frameborder=0
                src="https://apis.map.qq.com/tools/locpicker?search=1&type=1&key=UAOBZ-LC7WF-FAXJG-NXLGD-B3SDK-JKBTK&referer=myapp">
        </iframe>
    </v-app>

</template>

<script>
    export default {
        name: "qqmap",
        data() {
            return {
                'address':'地址'
            }
        },
        computed: {

        },
        methods: {
            initMap() {
                addEventListener('message', function (event) {
                    // 接收位置信息，用户选择确认位置点后选点组件会触发该事件，回传用户的位置信息
                    var loc = event.data;
                    if (loc && loc.module == 'locationPicker') {
                        //防止其他应用也会向该页面post信息，需判断module是否为'locationPicker'
                        console.log('location', loc);
                    }
                }, false);
            }
        },
        created(){
            this.initMap();
        },
        mounted() {

        },
        watch:{
        },
    }
</script>

